<template>
        <div class="flex">
          <aside>
              <Sidebar :list="list" base='wellcompletion' title="完井数据"/>
          </aside>
          <section>
              <router-view :route="route"/>
          </section>
        </div>
      </template>
      
      <script>
      import { mapGetters } from 'vuex'
      import Sidebar from '@/components/common/Sidebar';
      export default {
        data () {
          return {
              list: [
                  {
                      txt: '套管数据',
                      url: 'casingdata'
                  },
                  {
                      txt: '套管串',
                      url: 'casingstring'
                  },
                  {
                      txt: '固井作业',
                      url: 'welloperation'
                  },
                  {
                      txt: '完井口装置',
                      url: 'completiongear'
                  },
                  {
                      txt: '套管试压',
                      url: 'casingpressure'
                  },
                  {
                      txt: '井径数据',
                      url: 'caliperdata'
                  },
                  {
                      txt: '钻完井基础数据',
                      url: 'wellbasicdata'
                  }
              ]
          }
        },
      
        components: {
            Sidebar
        },
      
        computed: {
          ...mapGetters({
            route: 'route'
          }),
        },
      
        mounted: function() {},
      
        methods: {}
      }
      </script>
      
      <style lang='scss' scoped>
      .flex {
          height: 100%;
      }
      aside {
          width: 260px;
      }
      section {
          width: calc(100% - 260px);
      }
      </style>
      


